.popup__container {
  max-width: 430px;
  background: #FFFFFF;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  color: #000000;
  position: relative;
}

@media (max-width: 600px) {
  .popup__container {
    max-width: 282px;
  }
}

.popup__container:hover {
  cursor: default;
}